.current {
  color: green;
}

/* Define keyframes for animations */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideInLeft {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideInRight {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

/* Base styles */
body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: url('/assets/images/background.jpg') no-repeat center center fixed;
    background-size: cover;
    position: relative;
    animation: fadeIn 2s ease-in-out;
    color: #333333;
}

body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: inherit;
    filter: blur(8px);
    z-index: -1;
}

header {
    background-color: rgba(42, 157, 244, 0.9); /* Primary color with transparency */
    color: white;
    text-align: center;
    padding: 2rem 0;
    position: relative;
    z-index: 1;
    animation: fadeIn 2s ease-in-out;
}

header h1 {
    font-size: 2.5rem;
    margin: 0.5rem 0;
}

header p {
    font-size: 1.2rem;
    margin: 0;
}

.profile-pic {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    border: 5px solid white;
    margin-bottom: 1rem;
    animation: fadeIn 2s ease-in-out;
}

main {
    padding: 2rem 1rem;
    max-width: 900px;
    margin: 3rem auto;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    position: relative;
    z-index: 1;
    animation: fadeIn 2s ease-in-out;
}

section {
    margin-bottom: 2rem;
    opacity: 0;
    animation: fadeIn 2s ease-in-out forwards;
}

h2 {
    border-bottom: 3px solid #1a73e8; /* Secondary color */
    padding-bottom: 0.5rem;
    margin-bottom: 1.5rem;
    font-size: 1.8rem;
    animation: slideInLeft 1.5s ease-in-out;
    color: #1a73e8; /* Secondary color */
}

p, ul {
    font-size: 1rem;
    line-height: 1.6;
}

ul {
    list-style-type: none;
    padding: 0;
}

ul li {
    background-color: #f0f0f0;
    margin: 0.5rem 0;
    padding: 0.75rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    animation: slideInRight 1s ease-in-out;
    transition: background-color 0.3s;
}

ul li:hover {
    background-color: #f4a261; /* Accent color on hover */
}

footer {
    text-align: center;
    padding: 1rem;
    background-color: rgba(26, 115, 232, 0.9); /* Secondary color with transparency */
    color: white;
    animation: fadeIn 2s ease-in-out;
}

footer p {
    margin: 0;
    font-size: 1rem;
}
